header{
    height: 100vh;
    padding-top: 5rem;
    overflow: hidden;
}

.header_container{
    text-align: center;
    height: 100%;
    position: relative;
}
.text-light{
    font-size: larger;
    font-weight: 600;
    color: rgb(83, 224, 219);
}

/* =============== CTA =========== */
.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* =============== Socials =========== */
.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

/* .header_socials::after{
    content: '';
    width: 2px;
    height: 2rem;
    background: var(--color-primary);
}  */

/* =============== ME =========== */
.me{
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 4rem 0 0 0;
}

/* =============== Scroll Down =========== */
.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform:  rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* ============ Media Queries (Medium Devices) ============ */
@media screen and (max-width: 1024px) {
    header{
        height: 100vh;
     }
}

/* ============ Media Queries (Small Devices) ============ */
@media screen and (max-width: 600px) {
   header{
    height: 100vh;
   }
   .header_socials, .scroll_down{
    display: none;
   }
}